import React, { useState, useEffect } from 'react';
import OHRITextObs from './inputs/ohri-text-obs.component';
import OHRIRadioObs from './inputs/OHRI-radio-obs.component';
import OHRICheckboxObs from './inputs/OHRI-checkbox-obs.component';
import OHRISelectObs from './inputs/ohri-select-obs.component';
import OHRIDateObs from './inputs/OHRI-date-obs.component';
import { Form, Formik } from 'formik';
import * as Yup from 'yup';
import {
  Row,
  Column,
  UnorderedList,
  ListItem,
  Link,
  Button,
  Accordion,
  AccordionItem,
  Tabs,
  Tab,
} from 'carbon-components-react';
import styles from '../../styles.scss';
import { TextNewLine32 } from '@carbon/icons-react';

const OHRIForm = function ({ pages }) {
  const [fields, setFields] = useState(pages[0].sections[0].questions);
  const [textValue, setTextValue] = useState('');
  const [radioValue, setRadioValue] = useState('');
  const [dateValue, setDateValue] = useState('');
  const initialValues = {};

  const renderQuestions = (questions) =>
    questions.map((question, index) => {
      const type = question.questionOptions.rendering;

      if (type == 'number') {
        return <OHRITextObs questions={question} onChange={handleTextInput} key={index} />;
      } else if (type == 'radio') {
        return <OHRIRadioObs questions={question} onChange={handleRadioInput} key={index} />;
      } else if (type == 'checkbox') {
        return <OHRICheckboxObs questions={question} onChange={handleCheckboxInput} key={index} />;
      } else if (type == 'select') {
        return <OHRISelectObs questions={question} onChange={handleSelectInput} key={index} />;
      } else if (type == 'date') {
        return <OHRIDateObs questions={question} onChange={handleDateInput} key={index} />;
      }
    });

  const renderSections = (sections) =>
    sections.map((section, index) => (
      <AccordionItem title={section.label} key={index} open={section.isExpanded == 'true' ? true : false}>
        {renderQuestions(section.questions)}
      </AccordionItem>
    ));

  const renderPages = (pages) =>
    pages.map((page, index) => (
      <Tab id={index.toString()} label={page.label} key={index}>
        <Accordion>{renderSections(page.sections)}</Accordion>
      </Tab>
    ));

  const renderSidemenu = (pages) => (
    <div className={styles['nav-sidebar']}>
      {pages.map((page, index) => (
        <Link href="#" key={index}>
          &#8627; {page.label}
        </Link>
      ))}
    </div>
  );

  const handleTextInput = (event: React.ChangeEvent<HTMLInputElement>) => {
    // return event;
    setTextValue(event.target.value);
    // eslint-disable-next-line no-console
    // console.log(textValue);
  };

  const handleRadioInput = (event: React.ChangeEvent<HTMLInputElement>) => {
    // return event;
    setRadioValue(event.target.value);
    // eslint-disable-next-line no-console
    console.log(radioValue);
  };

  const handleDateInput = (event: React.ChangeEvent<HTMLInputElement>) => {
    // return event;
    setDateValue(event.target.value);
    // eslint-disable-next-line no-console
    console.log(dateValue);
  };

  const handleSelectInput = (event: React.ChangeEvent<HTMLInputElement>) => {
    // return event;
    // setDateValue(event.target.value);
    // eslint-disable-next-line no-console
    // console.log(dateValue);
  };

  const handleCheckboxInput = (event: React.ChangeEvent<HTMLInputElement>) => {
    // return event;
    // setDateValue(event.target.value);
    // eslint-disable-next-line no-console
    // console.log(dateValue);
  };

  const handleFormSubmit = () => {
    return;
  };

  return (
    <>
      <Row>
        <Column lg={{ span: 3 }}>
          {renderSidemenu(pages)}
          <Button kind="primary" className={styles['left-panel-button']}>
            Create Record
          </Button>
          <Button kind="tertiary" className={styles['left-panel-button']}>
            Cancel
          </Button>
        </Column>
        <Column lg={{ span: 9 }}>
          <Formik initialValues={initialValues} validationSchema={Yup.object({})} onSubmit={handleFormSubmit}>
            {(props) => (
              <Form>
                <Tabs type="container">{renderPages(pages)}</Tabs>
              </Form>
            )}
          </Formik>
        </Column>
      </Row>
    </>
  );
};

export default OHRIForm;
